<template>
  <div>
    <v-container>
      <v-row>
        <v-col cols="12">
          <!-- data table -->
          <crud-data-table
            :table="{
              heading: $t('crud.table.headings.blog.comments'),
              search: true,
              showSelect: true
            }"
            :headers="headers"
            :actions="{
              delete: true
            }"
            :fetch-data="{
              url: `/api/comments/${$route.params.postId}`,
              data: 'comments'
            }"
            :delete-data="{
              url: '/api/comments/:param',
              param: '_id',
              reload: true
            }"
            :delete-multiple-data="{
              url: '/api/comments/delete',
              arrayOf: '_id',
              reload: true
            }"
          >
            <template v-slot:[`item.replies`]="{ item: i }">
              <v-btn
                link
                rounded
                color="primary"
                small
                :to="'/dashboard/blog/replies/' + i._id"
                >{{ i.replies.length }}
                {{
                  i.replies.length === 1
                    ? $t('replies.singular')
                    : $t('replies.plural')
                }}</v-btn
              >
            </template>
          </crud-data-table>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
export default {
  middleware: ['pagePermission'],
  components: {
    CrudDataTable: () => import('@/components/crud/CrudDataTable')
  },
  data() {
    return {
      headers: [
        { text: this.$t('crud.table.headers.text'), value: 'text' },
        { text: this.$t('crud.table.headers.replies'), value: 'replies' },
        {
          text: this.$t('crud.table.headers.actions'),
          value: 'actions',
          sortable: false
        }
      ]
    }
  }
}
</script>

